<template>
    <div class="center">
        <div class="center_content">
            <div class="center_content_title">
                <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
                <span>{{pageTitle}}</span>
            </div>
            <div class="table-container add-questionnaire">
                <el-form :model="form" :rules="rules" ref="form">
                    <p class="small-title-tip">问卷基础信息</p>
                    <el-form-item label="问卷类型" >
                        <el-input v-model="typeInput" disabled >
                        </el-input>
                    </el-form-item>
                     <el-row>
                        <el-col :span="12">
                             <el-form-item label="问卷名称" prop="formName">
                                <el-input v-model="form.formName" placeholder="请输入问卷名称">
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="调查对象" prop="type">
                                <el-select v-model="form.type" placeholder="请选择">
                                    <el-option v-for="item in genreOptions" :key="item.id" :label="item.name" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                     </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="调查版本" prop="ext1">
                                <el-select v-model="form.ext1" placeholder="请选择" @change="versionChange">
                                    <el-option v-for="item in versionArr" :key="item.id" :label="item.name" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                         <el-col :span="12" v-if="type === 'graduate' ">
                            <el-form-item label="调查年级" prop="ext2">
                                <el-select v-model="form.ext2" placeholder="请选择">
                                    <el-option v-for="item in classGradesOptions" :key="item" :label="item" :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12" v-if="type === 'course' ">
                            <el-form-item label="评价课程" prop="ext3">
                                <el-select v-model="form.ext3" @change="courseChange" placeholder="请选择">
                                    <el-option v-for="item in courseArr" :key="item.id" :label="item.name" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row v-if="type === 'course'">
                        <el-col :span="12" >
                            <el-form-item  label="发起学年" prop="ext2">
                                <el-select v-model="form.ext2"  @change="getChooseClass" placeholder="请选择" >
                                    <el-option v-for="item in gradesArr" :key="item" :label="item" :value="item"></el-option>
                                    <!-- <el-option v-for="item in gradesArr" :key="item" :label="`${item} - ${item + 1}学年`" :value="item"></el-option> -->
                                </el-select>
                            </el-form-item>
                        </el-col>
                         <el-col :span="12">
                            <el-form-item label="选择班级" prop="classIds">
                                <el-select v-model="classIds" @change="classChange" :multiple="off" placeholder="请选择">
                                    <el-option v-for="item in classList" :key="item.id" :label="item.name" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <el-form-item class="first" label="调查时间" prop="">
                            <el-date-picker
                                v-model="times"
                                @change="timeChange"
                                type="daterange"
                                range-separator="-"
                                start-placeholder="设置开始时间"
                                end-placeholder="设置结束时间">
                                </el-date-picker>
                        </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <el-form-item :label="isAutoTitle" class="start">
                                <template>
                                    <el-checkbox v-model="isAuto"></el-checkbox>
                                </template>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div class="add-questionnaire_footer">
                    <button @click="create('form')" type="button" class="el-button set-btn"><span>{{status ==='add' ? '创建问卷' : '编辑问卷'}}</span></button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {formatDate} from '@/utils/utils.js'
export default {
    name: 'AddQuestionnaire',
    components: {},
    data() {
        return {
            pageTitle: '新建问卷',
            type:this.$route.query.type,
            status:this.$route.query.status,
            typeInput:'',
            times:[],
            isAuto:false,
            isAutoTitle:"",
            genreOptions:[
                {name:'学生', id:'0'},
                {name:'教师', id:'1'}
            ],
            classIds:[],
            off:true,
            form: {
                formName:'',
                formId:undefined,
                startTime:'',
                endTime:'',
                type:'0',//调查对象
                ext1:'', //调查版本
                ext2:undefined,//调查年级 发起学年
                ext3:undefined, //评价课程
                autoSend: undefined,  //不开启为0，开启为1
                ext4:undefined, //不开启教师提醒为0，开启教师提醒为1
                classIds:undefined //选择班级
            },
            rules: {
                formName: [{
                    required: true,
                    message: '请输入问卷名称',
                    trigger: 'blur'
                }],
                type: [{
                    required: true,
                    message: '请选择调查对象',
                    trigger: 'blur'
                }],
                ext1: [{
                    required: true,
                    message: '请选择调查版本',
                    trigger: 'blur'
                }],
                ext2: [{
                    required: true,
                    message: '请选择',
                    trigger: 'blur'
                }],
                ext3: [{
                    required: true,
                    message: '请选择评价课程',
                    trigger: 'blur'
                }],
                classIds: [{
                    required: true,
                    message: '请选择班级',
                    trigger: 'blur'
                }]
            },
            gradesArr: [],
            versionArr:[],
            courseArr:[],
            classList:[],
            courseName:'',
            classGradesOptions:[],
            api:{
                versionAll: scope => `/basic/version/all/${scope}`, //版本
                //classGrades:'/basic/achieve/grades' , //调查年级
                classGrades:scope => `/basic/achieve/gradesbyVersionID/${scope}` , //调查年级(新)
                schedulingGrades:scope => `basic/scheduling/grades/${scope}`, //发起学年(旧版)
                scoreAnalysisGrades:scope => `/teacher/scoreAnalysis/grade/${scope.ext1}/${scope.ext3}`, //发起学年(新版)
                //courseAll: scope => `/basic/course/all/${scope.specialtyId}/${scope.versionId}`, //评价课程 -旧版
                courseAll: scope => `/basic/course/formAll/${scope.specialtyId}/${scope.versionId}`, //评价课程 -新版
                formChooseClass:scope => `/form/rest/FormMainBaseBusiness/formChooseClass/v1`,//课程质量-选择班级
                addRequirementsMet:'/form/rest/FormMainBaseBusiness/addRequirementsMet/v1', //新增毕业要求达成评价问卷
                editRequirementsMet:'/form/rest/FormMainBaseBusiness/modifyRequirementsMet/v1', //修改毕业要求达成评价问卷
                info:'form/rest/FormMainBaseBusiness/displayRequirementsMet/v1', //毕业要求问卷详情
                addCourseQuality:'/form/rest/FormMainBaseBusiness/addCourseQuality/v1', //新增课程质量分析
                editCourseQuality:'/form/rest/FormMainBaseBusiness/modifyCourseQuality/v1', //修改课程质量分析
                infoTwo:'/form/rest/FormMainBaseBusiness/displayCourseQuality/v1' //课程质量问卷详情
            }
        }
    },
    created() {
        this.off=localStorage.getItem('roleName') === '课程负责人' ? true : false
    },
    mounted() {
        this.initData()
    },
    methods: {
        initData(){
            this.version()
            if(this.type === 'graduate'){
                this.typeInput='毕业要求达成评价'
                this.isAutoTitle='是否开启自动发送'
               
            }else{
                this.typeInput='课程质量评价'  
                this.isAutoTitle='开启老师发起评价提醒'
                //this.schedulinggrades()
            }
            if(this.status ==='edit'){
                this.details() 
            }

        },
        details(){
            const url= this.type === 'graduate' ?  this.api.info : this.api.infoTwo
            const pointType=this.$route.query.point === 'template' ? 2 : 1
            const postData={
                formId: this.$route.query.id,
                type:this.type === 'graduate' ? undefined : pointType
            }
            this.$post(url, postData).then(res =>{
                if(res.code ===200){     
                    const store=res.data[0] || {}
                    this.form.formName=store.formName
                    this.form.ext1=store.ext1
                    this.form.ext2=store.ext2
                    this.form.formId=store.id
                    this.form.type=`${store.formType}`
                    this.form.startTime=formatDate(store.startTime , 'yyyy-MM-dd')
                    this.form.endTime=formatDate(store.endTime , 'yyyy-MM-dd')
                    this.times=[store.startTime, store.endTime]
                    this.isAuto=store.ext5 === '0' ? false : true
                    const sendClassids=store.sendClassids || []
                    const classIdsArr=[]
                    sendClassids.forEach(item =>{
                        classIdsArr.push(item.id)
                    })
                    this.classIds=this.off ? classIdsArr : classIdsArr.join(',')
                    this.form.classIds=this.off ? this.classIds.join(',') : this.classIds
                   
                     if(this.type === 'graduate'){
                            this.classGrades()
                        }
                    if(this.type === 'course'){
                        this.form.ext3=store.ext3
                        this.changetype()
                        this.scoreAnalysisGrades()
                        this.getChooseClass()
                    }
                }
            })
        },
        timeChange(){
            this.form.startTime=formatDate(Date.parse(this.times[0]), 'yyyy-MM-dd')
            this.form.endTime=formatDate(Date.parse(this.times[1]), 'yyyy-MM-dd')
        },
        create(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(this.status ==='add'){
                        this.userAdd()
                    }else{
                        if(this.$route.query.point === 'template'){
                             this.userAdd()
                        }else{
                             this.userEdit() 
                        }
                        
                    }
                    
                }
            });
        },
        courseChange(e){
          this.scoreAnalysisGrades()
          this.courseArr.forEach(item =>{
              if(item.id === e){
                    this.courseName=item.name
                    return
              } 
          })
        },
        classChange(){
            if(typeof this.classIds === 'string'){
                  this.form.classIds=this.classIds
            }else{
                 this.form.classIds=this.classIds.join(',')
            }
        },
        userAdd(){
          if( this.type === 'graduate'){
               this.form.autoSend=this.isAuto ? 1 : 0
          }else{
             this.form.ext4= this.isAuto ? 1 : 0
          }    
          const url = this.type === 'graduate' ? this.api.addRequirementsMet : this.api.addCourseQuality
          this.$post(url, this.form).then(res =>{
            if(res.code === 200){
                this.$router.push({
                    path: '/evaluationAndFeedback/editQuestionnaire',
                    query:{
                            id:res.data.formId,
                            oldId:this.$route.query.id,
                            entryType:this.type,
                            courseName:this.courseName
                        }
                })
            }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.msg
                })
            }
          })
        },
        userEdit(){
          if( this.type === 'graduate'){
               this.form.autoSend=this.isAuto ? 1 : 0
          }else{
             this.form.ext4= this.isAuto ? 1 : 0
          }    
          const url = this.type === 'graduate' ? this.api.editRequirementsMet : this.api.editCourseQuality
          this.$post(url, this.form).then(res =>{
            if(res.code === 200){
                this.$router.push({
                    path: '/evaluationAndFeedback/editQuestionnaire',
                    query:{
                            id:this.form.formId,
                            entryType: this.type,
                            courseName:this.courseName
                        }
                })
            }else{
                    this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.msg
                })
            }
          })
        },
        versionChange(){
            if(this.type === 'graduate'){
                this.classGrades()
            }
            if(this.type === 'course'){
                this.changetype()
            }
        },
        //  发起学年 (旧版)
        schedulinggrades() {
            this.$get(this.api.schedulingGrades(localStorage.getItem('specialId'))).then(res => {  
                this.gradesArr = res.data
            })
        },
        //发起学年（新版）
        scoreAnalysisGrades(){
            const url=this.api.scoreAnalysisGrades({
                ext1:this.form.ext1,
                ext3:this.form.ext3
            })
            this.$get(url).then(res =>{
                this.gradesArr = res
            })
        },
        //版本
        version(){
            this.$get(this.api.versionAll(localStorage.getItem('specialId'))).then(res=>{
                this.versionArr = res.data
            })
        },
        //调查年级
        classGrades(){
           this.$get(this.api.classGrades(this.form.ext1)).then(res=>{
                this.classGradesOptions = res.data
            })  
        },
        //评价课程
        changetype(){
            const postData={
                specialtyId:localStorage.getItem('specialtyId'),
                versionId:this.form.ext1
            }
            this.$get(this.api.courseAll(postData)).then(res=>{
                this.courseArr = res.data
            })
        },
        //选择班级
        getChooseClass(){
            const postData={
                versionId:this.form.ext1,
                courseId:this.form.ext3,
                grade:this.form.ext2
            }
            this.$get(this.api.formChooseClass(), postData).then(res=>{
               this.classList=res.data
            })
        }

    }
}
</script>

<style lang="scss">
@import '../../styles/center.scss';

.add-questionnaire {
    width: 100%;

    .el-input {
        width: 230px;
    } 
    .el-form-item__label {
        width: 100px !important;
    }
    .el-form-item__error{
        left:100px;
    }
    .start{
         .el-form-item__label {
            width: 180px !important;
        }
    }
    .add-questionnaire_footer {
        padding-top: 40px;

        .set-btn {
            color: #03B187;
            border-color: #03B187;
            background: #fff;
            outline: 0;
            margin-left: 50px;
        }

        .set-btn:hover {
            background: #fff;
        }
    }

}
</style>



